import React from 'react'
import { connect } from 'react-redux'
import s from './Basket.module.css'
import trash from '../../img/trash.jpg'
import {delBasket,clearBasket, setHistory} from '../reducers/reducers'
import $ from 'jquery'
import BasketForm from './BasketForm'

class BasketContainer extends React.Component{
    
    data(e){
            
        this.props.setHistory([this.props.basket, e])
        this.props.clearBasket()
        $("#modal").slideToggle()
    }

    render(){
        function arraySum(array){
            var sum = 0;
            for(var i = 0; i < array.length; i++){
                sum += array[i];
                }
                return sum
            }
        
        

        return(
            <div style={{paddingTop: 30, paddingLeft: 20}}>
                {this.props.basket.length>0 &&
                <div style={{border: '3px solid blue' , paddingLeft: 50, paddingTop: 30, paddingBottom: 100}}>
                    
                    {this.props.basket.map(basket=>
                        <div className={s.box}>

                            <div style={{marginTop: 30, marginLeft: 50,  width: 300, height: 200, textAlign: 'center', float: 'left'}}>
                                    <img src={basket.img} style={{ height: 200}}/>
                            </div>

                            <div style={{width: 500, height: 190, float: 'left', marginTop: 30, textAlign: 'center',paddingTop: 10}}>
                                <div>
                                    <h3>{basket.name}</h3>
                                </div>
                                <div style={{marginTop: 20}}>
                                    {basket.discription}
                                </div>    
                            </div>
                            <div style={{ width: 200, height: 200, float: 'left', marginTop: 30, textAlign: 'center'}}>
                                <div style={{marginTop: 70}}>

                                    <div style={{background: 'blue', color: 'white', width: 40, height: 40, marginLeft: 30, float:'left', borderRadius: 10, fontSize: 30}}>-</div>
                                    <div style={{float: 'left', fontSize: 30, marginLeft:20}}>
                                        {basket.count}
                                    </div>
                                    <div style={{background: 'blue', color: 'white', width: 40, height: 40, marginLeft: 20, float:'left', borderRadius: 10, fontSize: 30}}>+</div>

                                    <div onClick={()=>{this.props.delBasket(basket.id)}} >
                                        <img 
                                        src={trash} style={{width: 50, marginTop:20, borderRadius: 10}} />
                                    </div>  

                                </div>
                            </div>
                            <div style={{ width: 140, height: 100, float: 'left', marginTop: 30, textAlign: 'center',paddingTop: 100, fontSize: 20, fontWeight: 'bold', color: 'blue'}}>
                                {basket.price} руб
                            </div>
                            
                        </div>         
                    )}
                    
                    <div style={{color: 'blue', float: 'left'}}>
                        <h1>Итого: {arraySum( this.props.basket.map(price=>price.price))} руб</h1> 
                        
                    </div>
                    <div>
                        <button onClick={()=>$("#modal").slideToggle()} style={{width:200, height: 50, marginTop: 20, marginLeft: 700, background: '#18BAFF', borderRadius: 10, border: '0px', color: 'white', fontSize: 20}}>Оформить заказ</button>
                    </div>

                    <div id="modal" className={s.modal}>
                            <div onClick={()=>$("#modal").slideToggle()} style={{background: 'red', borderRadius: 10, width:30, textAlign: 'center', marginLeft: 10, marginTop: 10, fontSize: 20}}>
                                X
                            </div>
                            
                            <BasketForm onSubmit={this.data.bind(this)} arraySum={arraySum} basket={this.props.basket}/>
                            
                    </div>
                    
                </div>
    }
    {this.props.basket.length ===0 && <div> <h1 style={{textAlign: 'center'}}>Корзина пуста</h1></div>}
            </div>
                     
        )
    }
}

const mapStateToProps=(state)=>({
    basket: state.reducersState.basket
})

export const Basket = connect(mapStateToProps, {delBasket,clearBasket, setHistory})(BasketContainer)