export const reducersState = (state = initialState, action)=>{
    switch(action.type){
        case GET_THINGS:{
            return {...state, things: []}
        }
        case SET_BASKET:{
            return {...state, basket: [...state.basket, action.trade]}
        }
        case DEL_TRASH:{
            return {...state, basket: [...state.basket.filter(id=> id.id != action.id)]}
        }
        case SET_HISTORY:{
            return {...state, history: [...state.history, action.history]}
        }
        case CLEAR_BASKET:{
            return {...state, basket: []}
        }

        
        default:
            return state
    }
}


export const setBasket=(trade)=>({type: SET_BASKET, trade})
export const delBasket=(id)=>({type: DEL_TRASH, id})
export const setHistory=(history)=>({type: SET_HISTORY, history})
export const clearBasket=()=>({type: CLEAR_BASKET})


