import React from 'react'
import { Field, reduxForm } from 'redux-form'
import $ from 'jquery'




const BasketForm1=(props)=>{
    return(
        <form onSubmit={props.handleSubmit}>
             <div style={{paddingLeft: '10%', paddingTop:'10%', fontSize: 20}}>
                            <div style={{marginBottom: 10, fontSize: 30, color: 'blue', fontWeight: 'bold'}}>Сумма заказа: {props.arraySum(props.basket.map(price=>price.price))} руб</div>
                                <div >Имя: <Field name='firstname' component='input' style={{marginLeft: 60, width: 250, height:20, fontSize: 20, marginTop: 20}}></Field> </div>
                                <div>Адрес: <Field name='adress' component='input' style={{marginLeft: 41, width: 250, height:20, fontSize: 20, marginTop: 20}}></Field></div>
                                <div>Телефона: <Field name='phone' component='input' style={{marginLeft: 10, width: 250, height:20, fontSize: 20, marginTop: 20}}></Field></div>
                                
                                <button style={{width: 250, height: 50, marginLeft: 100, marginTop: 100, background: '#18BAFF', borderRadius: 10, border: 0, color: 'white', fontSize: 20 }}> Отправить</button>
                                
                            </div>
        </form>
    )
}


const BasketForm = reduxForm({form: 'basket'})(BasketForm1)

export default BasketForm