import React from 'react'
import { connect } from 'react-redux'
import s from '../Basket/Basket.module.css'



class HistoryContainer extends React.Component{
    render(){
        function arraySum(array){
            var sum = 0;
            for(var i = 0; i < array.length; i++){
                sum += array[i];
                }
                return sum
            }
        return(
            <div style={{marginTop: 50}}>
                <h1 style={{textAlign: 'center'}}>История заказов</h1>
            {this.props.history.map(history=>
                <div style={{border: '3px solid blue' , paddingLeft: 50, paddingTop: 30, paddingBottom: 100, marginTop:50}}>
                    
                    <div>
                        <div>
                        {history[0].map(history1=>
                            
                        <div className={s.box}>
                            {/* {console.log(history1)} */}
                            <div style={{marginTop: 30, marginLeft: 50,  width: 300, height: 200, textAlign: 'center', float: 'left'}}>
                                    <img src={history1.img} style={{ height: 200}}/>
                            </div>

                            <div style={{width: 500, height: 190, float: 'left', marginTop: 30, textAlign: 'center',paddingTop: 10}}>
                                <div>
                                    <h3>{history1.name}</h3>
                                </div>
                                <div style={{marginTop: 20}}>
                                    {history1.discription}
                                </div>    
                            </div>
                            <div style={{ width: 200, height: 200, float: 'left', marginTop: 30, textAlign: 'center'}}>
                                <div style={{marginTop: 70}}>

                                    
                                     

                                </div>
                            </div>
                            <div style={{ width: 140, height: 100, float: 'left', marginTop: 30, textAlign: 'center',paddingTop: 100, fontSize: 20, fontWeight: 'bold', color: 'blue'}}>
                                {history1.price} руб
                            </div>
                            
                        </div>
                        
                        )} 
                        </div>
                        </div>
                        <div style={{fontSize: 30, fontWeight: 'bold', color: 'blue', float: 'left'}}>Итого: {arraySum(history[0].map(price=>price.price))} руб</div>
                        <div style={{marginLeft: 500, fontWeight: 'bold', fontSize: 30, color: 'blue'}}>
                            <div>
                                Имя: {history[1].firstname}
                            </div>
                            <div>
                                Адрес: {history[1].adress}
                            </div>
                            <div>
                                Телефон: {history[1].phone}
                            </div>
                        </div>
                        </div>
                    )}
                    
            </div>
        )
    }
}

const mapStateToProps=(state)=>({
    history: state.reducersState.history
})

const History = connect(mapStateToProps)(HistoryContainer)

export default History