import React from 'react'
import { connect } from 'react-redux'
import s from './Shoes.module.css'
import { setBasket } from '../reducers/reducers'
import $ from 'jquery'



class ShoesContainer extends React.Component {

    addBasket(e){
        this.props.setBasket(e)
        $('#add').fadeIn(300)
        $('#add').fadeOut(300)
    }



    render() {
        
        return (
            <div style={{ marginTop: 20, textAlign: 'center' }}>

                <div id='add' className={s.modalAdd}>
                    <div style={{ marginTop: 30, fontSize: 30, color: 'white' }}>
                        Товар добавлен!
                   </div>
                </div>

                <h1 style={{ textAlign: 'center' }}>Обувь</h1>

                {this.props.shoes.map(thing =>
                    <div className={s.box}>
                        <div style={{ paddingTop: 5 }}>
                            <h3>{thing.name}</h3>
                        </div>
                        <div>
                            <img src={thing.img} style={{ width: 'auto', height: 200 }}></img>
                        </div>
                        <div style={{ paddingTop: 10, height: 100 }}>
                            {thing.discription}
                        </div>

                        <div style={{ marginBottom: 20, fontSize: 30, fontWeight: 'bold', color: "blue" }}>
                            {thing.price} руб
                        </div>

                        <div>
                            <button onClick={() => this.addBasket(thing)} style={{ width: 250, height: 50, borderRadius: 10, background: '#18BAFF', border: '0px', fontSize: 20, color: 'white' }}>Добавить в корзину</button>
                        </div>
                    </div>
                )}

            </div>
        )
    }
}


const mapStateToProps = (state) => ({
    shoes: state.reducersState.shoes
})

export const Shoes = connect(mapStateToProps, { setBasket })(ShoesContainer)