import React from 'react'
import s from './Header.module.css'
import catalog from '../../img/catalog.png'
import $ from 'jquery'
import { NavLink } from 'react-router-dom'
import loop from '../../img/loop.png'
import { connect } from 'react-redux'
import history from '../../img/history.jpg'


class HeaderContainer extends React.Component{
    render(){
    const Catalog=()=>{
        $('#catalog').fadeToggle(300)
    }

    return (
        <div className={s.header}>
            
            
            <div style={{ paddingTop: 10, paddingLeft: 50}}>

                <div style={{textAlign: 'center', marginTop: -20, marginBottom: -30, color: 'white' }}><h1>Ivan`Ko Shop</h1></div>

                <div className={s.catalog} onClick={Catalog}>
                    <img src={catalog} style={{width:50, height:50}}></img>
                </div>

                <div style={{paddingTop: 30, paddingLeft: 200}}>
                    <input style={{height: 30, width: 900, fontSize: 20, float: 'left'}} placeholder='Search...'></input><img src={loop} className={s.loop} style={{float:'left'}}/>
                    <NavLink to='/basket' style={{float: 'left', marginLeft: 40}}>
                        <div className={s.basket} >
                            {this.props.count.length>0 &&
                            <div style={{background: 'red', color: 'white', width: 25,height: 20, textAlign: 'center', borderRadius: 10, fontWeight: 'bold'}}>
                                {this.props.count.length}
                            </div>
                            }
                        </div>
                    </NavLink>

                    <div>
                        <NavLink to='/history'>
                            <img src={history} style={{width: 50, borderRadius: 50}} />
                        </NavLink>
                    </div>
                
                </div>

                
            </div>
            
            <div id='catalog' className={s.catalogShow}>
                <div className={s.catalogBox}>
                    <h1>Каталог</h1>
                    <div><NavLink onClick={()=>$('#catalog').fadeToggle(300)} to='/things' style={{textDecoration: 'none', color: 'black'}}><h3>Одежда</h3></NavLink></div>
                    <div><NavLink onClick={()=>$('#catalog').fadeToggle(300)} to='/shoes' style={{textDecoration: 'none', color: 'black'}}><h3>Обувь</h3></NavLink></div>
                    <div><h3>Акксесуары</h3></div>
                </div>
            </div>

        </div>
    )
}}

const mapStateToProps = (state)=>({
    count: state.reducersState.basket
})


const Header = connect(mapStateToProps)(HeaderContainer)

export default Header